.coin-container {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 1200px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 250px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
}

.coin-price {
    width: 120px;
}

.coin-volume {
    width: 250px;
}

.coin-marketcap {
    width: 255px;
}

.coin-percent {
    width: 20%;
}

.symbol {
    padding-left: 30px;
    width: 20px;
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}